body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

#generator-form {
   width: 500px;
   padding: 20px;
   margin: 10px auto;
   display: -webkit-flex;
   display: flex;
}
#generator-form div {
   margin: 10px;
}
#generator-form input {
   width: 20px;
   font-size: 18px;
   padding: 5px;
}

#sheet {
  width: 700px;
}

.grid {
   margin: 10px 20px;
   width: 600px;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-around;
           justify-content: space-around;
}

.grid  p {
   display: inline;
   text-align: left;
}

button {
   margin: 12px;
}

#take-attendance {
   width: 300px;
   margin: 30px;
}

